




























import util from '@/common/util'
import navigate from '@/components/navigator'
export default {
    data() {
        return {
            isShowBack:true,
            movieId:'',
            titleText:'短评',
            showStar:false,
            showShare:false,               
            pageIndex:1,
            totalCount:0,
            loadingText:'',
            commentList:[],
            loadAll:false,
            totalPage:0
        }
    },
    onLoad(option) {
        let self = this;
        self.movieId = option.movieId;
    },
    onReady(){
        let self = this;
        self.init();
    },
    onPullDownRefresh(){
        let self = this;
        self.onPullDownRefresh();
    },
    onReachBottom(){
        let self = this;
        self.pageIndex++;
        if(!self.loadAll){
            self.loadMore();
        }
    },
    methods: {
        init(){
            //获取短评
            let self = this;
            self.loadAll = false;
            return new Promise((resolve,rejected)=>{
                self.api.getMinComents({
                    data:{
                        pageIndex:self.pageIndex,
                        movieId:self.movieId                            
                    }
                }).then(res=>{
                    self.commentList = [];       
                    self.commentList = res.data.cts;
                    self.commentList.forEach((item,index)=>{
                        item.date = util.formatDate('Y-m-d H:i:s',item.cd);
                    })
                    self.totalCount = res.data.totalCount;
                    self.loadingText = '加载中...';
                    resolve();
                }).catch(rej=>{
                    rejected();
                })      
            })
        },
        loadMore(){
            //加载更多
            let self = this;
            self.api.getMinComents({
                data:{
                    pageIndex:self.pageIndex,
                    movieId:self.movieId                        
                }
            }).then(res=>{
                if(res.data.cts.length <= 0){
                    self.loadAll = true;
                    self.pageIndex = 1;
                    self.loadingText = '已加载全部';
                }else{
                    res.data.cts.forEach((item,index)=>{
                        item.date = util.formatDate('Y-m-d H:i:s',item.cd);
                    })
                    self.commentList = self.commentList.concat(res.data.cts);;
                    self.loadingText = '加载中...';                        
                }
            }).catch(rej=>{
                rejected();
            })   
        },
        onPullDownRefresh(){
            this.init().then(()=>{
                uni.stopPullDownRefresh();
            })
        }
    },
    components:{
        navigate
    }
}
